
.formControl.error input,
.error textarea {
    margin-bottom: 20px;
    border: 2px solid rgb(241, 216, 117);
    border-radius: 5px;
    box-shadow:
            0 6px rgb(222, 216, 86),
            0 3px 15px rgba(0,0,0,.4),
            inset 0 1px rgba(255,255,255,.3),
            inset 0 0 3px rgba(255,255,255,.5);
    transition: .2s;
}


.formControl.error span {
    color: red;
    border: solid red;
    margin-bottom: 20px;

}

.formSummaryError {
    border: red 1px solid;
    padding: 5px;
    color: red;
}