.nav {
    grid-area: n;
    background: linear-gradient(90deg, #346334, #A1A1A1);
    text-align: center;
    border-radius: 20px;
    margin-top: 10px;
}

.button {
    margin-top: 20px;

    display: inline-block;
  /*  font-weight: bold;*/
    color: #131212;
    text-decoration: none;
    text-shadow: 0 -1px rgba(0,0,0,.5);
    user-select: none;
    padding: .7em 1.5em;
    border: 1px solid rgb(80,32,0);
    border-radius: 5px;
    outline: none;
    background: rgb(224, 232, 64) linear-gradient(rgb(169, 219, 34), rgb(48, 95, 5) 80%);
    box-shadow:
            0 6px rgb(12, 75, 15),
            0 3px 15px rgba(0,0,0,.4),
            inset 0 1px rgba(255,255,255,.3),
            inset 0 0 3px rgba(255,255,255,.5);
    transition: .2s;
}
.button:hover {
    background: rgb(238, 236, 116) linear-gradient(rgb(241, 240, 235), rgb(237, 241, 109) 80%);
}
.button:active {

    background: rgb(232, 232, 94) linear-gradient(rgb(231, 227, 227) 20%, rgb(106, 104, 101));
    box-shadow:
            0 2px rgb(10, 66, 16),
            0 1px 6px rgba(0,0,0,.4),
            inset 0 1px rgba(255,255,255,.3),
            inset 0 0 3px rgba(255,255,255,.5);
    -webkit-transform: translate(0, 4px);
    transform: translate(0, 4px);
}

