.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200px;
  grid-template-areas: "h h" "n c";



  grid-template-rows: 160px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */


}

.app-wrapper-content {
  border-radius: 20px;
  padding: 10px;
  margin-left: 10px;
  margin-top: 10px;
  margin-right: 10px;
  grid-area: c;
  background: linear-gradient(90deg, #A1A1A1, #FFFF44 )


}



